.header {
    background: $off-black;
    color:white;
    margin-bottom: $m-size;
    padding: $m-size 0;
}

.header__title {
    font-size: $l-size;
    margin: 0;
}
.header__subtitle {
    color: $off-white;
    font-size: $m-size;
    font-weight: 500;
    margin: 0;
}
//* Big screens
@media (min-width: $pc-breakpoint) {
    .header  {
        margin-bottom: $xl-size;
    }
}
