.add-option-error {
    color: $off-white;
    font-style: italic;
    margin: $m-size 0 0 0;
    padding: 0 $m-size;

}
.form {
    display: flex;
    flex-direction: column;
    padding: $m-size;


    &__input {
        flex-grow: 1; //* form__input занимает все доступное пространство по сравнению с соседями
        background: $dark-blue;
        border:none;
        color:#fff;
        border-bottom: .3rem solid darken($dark-blue, 10%);
        padding: $s-size;
        margin: 0 0 $s-size 0;

    }
}
@media (min-width: $pc-breakpoint) {
    .form {
        flex-direction: row;
    }
    .form__input {
        margin: 0 $m-size 0 0;

    }
}
//todo min-width === apply styles AFTER screen width is 45 rem or more
//todo max-width === apply styles BEFORE screen width is 45 rem or less
