.option {
    border-bottom: 1px solid lighten($light-blue, 10%);
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    padding: $l-size $m-size;

    &__text {
        color: #fff;
        font-size: 2rem;
        font-weight: 500;
        margin: 0;
        word-break: break-all;
    }

}
