.big-button {
    background: $purple;
    border:none;
    border-bottom: .6rem solid darken($purple, 10%);
    color: #fff;
    font-weight: bold;
    font-size: $l-size;
    width: 100%;
    padding: 2.4rem;
    margin-bottom: $m-size;
}
.big-button:disabled {
    opacity: .4;
}

//* Button
.button {
    background: $purple;
    border:none;
    border-bottom: .3rem solid darken($purple, 10%);
    color:#fff;
    font-weight: 600;
    padding: $s-size;


}
.buttons__center {
    display: flex;
    justify-content: space-evenly;
}

button {
    cursor: pointer;
}
button:disabled {
    cursor: default;
}

.button--link {
    background: none;
    border: none;
    color: $off-white;
    padding: 0;
}
.button--cross {
    background: none;
    border: none;
    color: $off-white;
    font-weight: bold;
    font-size: 3.0rem;
}

//* Big screens
@media (min-width: $pc-breakpoint) {
    .big-button  {
        margin-bottom: $xl-size;
    }
}
